import React, { Component } from "react";
import Header from "../elements/header";
import Sidebar from "../elements/sidebar";
import { Link, Redirect } from "react-router-dom";

export default class Dashboard extends Component {
  renderConsultancySidebar = () => {
    if (
      localStorage.getItem("permission") == "Administrador" ||
      localStorage.getItem("permission") == "Cliente"
    ) {
      return (
        <div className="col-xl-3 col-sm-6 mb-3">
          <div className="card text-white bg-primary o-hidden h-100">
            <div className="card-body">
              <div className="card-body-icon">
                <i className="fas fa-fw fa-list"></i>
              </div>
              <div className="mr-5">Consultorias e Assessorias</div>
            </div>
            <Link
              className="card-footer text-white clearfix small z-1"
              to={"/consultancy"}
            >
              <span className="float-left">Detalhes</span>
              <span className="float-right">
                <i className="fas fa-angle-right"></i>
              </span>
            </Link>
          </div>
        </div>
      );
    }
  };

  renderStandardSidebar = () => {
    if (localStorage.getItem("permission") == "Administrador") {
      return (
        <div className="col-xl-3 col-sm-6 mb-3">
          <div className="card text-white bg-info o-hidden h-100">
            <div className="card-body">
              <div className="card-body-icon">
                <i className="fas fa-fw fa-list"></i>
              </div>
              <div className="mr-5">Gestão de Normas</div>
            </div>
            <Link
              className="card-footer text-white clearfix small z-1"
              to="/standard"
            >
              <span className="float-left">Detalhes</span>
              <span className="float-right">
                <i className="fas fa-angle-right"></i>
              </span>
            </Link>
          </div>
        </div>
      );
    }
  };

  render() {
    return (
      <div>
        <Header />
        <div id="wrapper">
          <Sidebar></Sidebar>
          <div id="content-wrapper">
            <div className="container-fluid">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to={"/dashboard"}>Dashboard</Link>
                </li>
                <li className="breadcrumb-item active">Overview</li>
              </ol>

              <div className="row">
                {this.renderConsultancySidebar()}
                {this.renderStandardSidebar()}
              </div>
            </div>

            <footer className="sticky-footer">
              <div className="container my-auto">
                <div className="copyright text-center my-auto">
                  <span>SIGO</span>
                </div>
              </div>
            </footer>
          </div>
        </div>
      </div>
    );
  }
}
